@use "../base/variables" as v;


@mixin bp-xs { 
  @media (min-width: 21.875em) {  /* 600px */
    @content;
  }
}
@mixin bp-sm { 
  @media (min-width: 37.5em) {  /* 600px */
    @content;
  }
}
@mixin bp-md {
  @media (min-width: 56.25em) {  /* 900px */
    @content;
  }
}
@mixin bp-lg {
  @media (min-width: 75em) {  /* 1200px */
    @content;
  }
}
@mixin bp-xl {
  @media (min-width: 93.75em) {  /* 1500px */
    @content;
  }
}

@mixin bp-nav {
  @media (min-width: 75em) {  /* 1200px */
    @content;
  }
}
